import React from 'react'
import { NavLink } from 'react-router-dom'

function History() {
    return (
        <div>
            <h1 className='text-center text-white fs-2' style={{ paddingTop: "10rem", height: "15rem" }}>History</h1>
            <div className="container mt-5 pt-5"></div>
            <nav className="navbar navbar-expand-lg" style={{ backgroundColor: "rgba(21,48,71,.9)" }}>
                <div className="container-fluid">
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavDropdown" style={{ justifyContent: "center" }}>
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold' exact='true' to="/crm/user/transfer/own-bank/beneficiaries"> BENEFICIARIES </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold' to="/crm/user/transfer/own-bank/within" > WITHIN FDA INDIA </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold' to="/crm/user/transfer/own-bank/otherbank" > OTHER BANK </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold' to="/crm/user/transfer/own-bank/wiretransfer" > WIRE TRANSFER</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className='nav-link fw-bold text-info shadow-lg' to="/crm/user/transfer/own-bank/history" >HISTORY</NavLink>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>

            <div className="container-fluid transferdashboard">
                <div className='mt-5'>
                    <div className="bene">
                        <table>
                            <thead>
                                <tr>
                                    <th>TRX</th>
                                    <th>ACCOUNT</th>
                                    <th>BANK</th>
                                    <th>AMOUNT</th>
                                    <th>CHARGE</th>
                                    <th>PAID AMOUNT</th>
                                    <th>STATUS</th>    
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td></td>
                                    <td></td>
                                    <td></td>
                                    <td>No Data Found</td>
                                    <td></td>
                                    <td></td>
                                    <td></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default History
